@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700,800,900&display=swap);
* {
  font-family: 'Nunito Sans', sans-serif;
  line-height: 1.5;
}

body {
  margin: 0px;
}

hr {
  margin: 0px;
  margin-top: 24px;
}

h1 {
  font-size: 60px;
  font-weight: normal;
  margin: 0px;
  margin-bottom: 28px;
  padding-top: 48px;
}

h2 {
  font-weight: normal;
  font-size: 20px;
}

.title-wrapper {
  margin-left: 400px;
  margin-right: 48px;
}

.projects-wrapper {
  text-align: left;
  margin-left: 400px;
  margin-right: 48px;
}

@media only screen and (max-width: 1160px) {
  .projects-wrapper {
    margin-left: 48px;
  }

  .title-wrapper {
    margin-left: 48px;
  }
}
.side-menu {
    border: 1px solid grey;
    width: 279px;
    top: 0px;
    bottom: 0px;
    position: fixed;
    overflow-y: auto;
    background-color: white;
    z-index: 2;
}

.side-menu-name {
    font-weight: bold;
    font-size: 18px;
    padding: 16px;
    padding-left: 24px;
    cursor: pointer;
}

.side-menu-item {
    padding: 12px;
    padding-left: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.side-menu-entry {
    padding: 12px;
    padding-left: 48px;
    cursor: pointer;
}

.side-menu-item:hover {
    background-color: whitesmoke;
}

.side-menu-entry:hover {
    background-color: whitesmoke;
}

.screen {
    width: 100%;
    height: 100%;
    top: 0px;
    bottom: 0px;
    position: fixed;
    background-color: black;
    opacity: 0.5;
}

.hamburger-icon {
    top: 0px;
    right: 0px;
    position: fixed;
    padding: 12px 15px 9px 15px;
    margin: 32px;
    box-shadow: 0 0 8px #888888;    
    border-radius: 50%;
    background-color: white;
    z-index: 2;
}
.project {
    max-width: 760px;
}

.link-wrapper {
    color: black;
    text-decoration: none;
}

.link-text {
    color: blue;
    text-decoration: none;
    margin: auto 12px;
    align-items: center;    

}

.link-icon {
    margin: 16px;
}

.link-container {
    display: flex;
    box-shadow: 0 0 3px #888888;    
    text-align: left;
    align-items: center;
    margin-right: 16px;
    margin-bottom: 16px;
    border-radius: 3px;
}

.link-container:hover {
    background-color: whitesmoke;
}

.divider {
    border-left: 1px solid lightgray;
    height: 44px;
}


.links-container {
    display: flex;
    flex-wrap: wrap;
}

.dark {
    color: white;
}

.caption {
    font-size: 12px;
    margin-top: 12px;
    margin-bottom: 0px;
    text-align: center;
}

.project-text {
    margin-top: 40px;
    margin-bottom: 24px;
}

.text-link {
    text-decoration: none;
    color: blue;
}

.media-demo {
    /* height: 512px; */
    display: block;
    margin: 48px auto;
    margin-bottom: 0px;
    
    border-width: 2px;
    border-style: solid;
    border-color: black;
    border-radius: 5px;

    max-width: 100%;
}

.title-wrapper {
    padding-top: 20vh;
    margin-left: 400px;
    /* padding-right: 48px; */
    padding-bottom: 256px;
    background-color: #202124;
}

.title-wrapper-wrapper {
    background-color: #202124;
}

.title-link {
    color: lightcyan;
    text-decoration: none;  

}

@media only screen and (max-width: 1160px) {
    .title-wrapper {
        margin-left: 48px;
    }
}
